import { useState } from "react";
export default function BloodPressureForm(initform) {

    const [form, setForm] = useState({});


    const date = (theDate) => {
        const d = new Date(theDate)
        return d.toLocaleDateString();
    }

    //FIXME
    //might need this for date picking
    //const DatePicker = () => {
    //    const [date, setDate] = useState('');
    //    const dateInputRef = useRef(null);

    const handleChange = (e) => {
        const name = e.target.name;
        const value = e.target.value;
        setForm({ ...form, [name]: value })
        //setClient(values => ({ ...values, [name]: value }));

    };

    async function handleSubmit(e) {
        e.prventDefault();

    }


    return (

        <div className="container card bg-white">
            <h3 className="card-title mt-3 text-center">Individual Blood Pressure Screening</h3>
            <hr></hr>

            {/* last bp from screening */}

            <form action="POST" onSubmit={handleSubmit}>
                <div className="row mb-3">
                    {/* Interaction Date */}
                    <div className="col-6">
                        <label htmlFor="date" className="form-label">Interaction Date:</label>
                        <input
                            value={form.date}
                            type="datetime-local"
                            className="form-control"
                            onChange={handleChange}
                            name="date"
                            required />
                    </div>
                </div>
                <div className="row mb-3">
                    {/* Bp screening */}
                    <div className="col-3">
                        <label htmlFor="systolic" className="form-label">Systolic</label>
                        <input
                            type="number" className="form-control"
                            value={form.systolic}
                            onChange={handleChange}
                            id="systolic"
                            name="systolic"
                            min='0'
                            max='250'
                            required
                        />
                    </div>
                    <div className="col-1"><span>/</span></div>
                    <div className="col-3">
                        <label htmlFor="diastolic" className="form-label">Diastolic</label>
                        <input
                            type="number" className="form-control"
                            value={form.diastolic}
                            onChange={handleChange}
                            id="diastolic"
                            name="diastolic"
                            min='0'
                            max='250'
                            required
                        />
                    </div>
                    <div className="col">
                        <label htmlFor="arm" className="form-label">L/R Arm</label>
                        <select className="form-select"
                            value={form.arm}
                            onChange={handleChange}
                            id="arm"
                            name="arm"
                        >
                            <option selected disabled>Please Select</option>
                            <option>Left</option>
                            <option>Right</option>
                        </select>
                    </div>
                </div>
                <div className="row mb-3">
                    <div className="col-6">
                        <label htmlFor="category" className="form-label">BP Category:</label>
                        <select className="form-select"
                            value={form.bp_category}
                            onChange={handleChange}
                            id="bp_category"
                            name="bp_category">
                            <option selected disabled>Please Select</option>
                            <option>Normal</option>
                            <option>Elevated</option>
                            <option>Hypertension Stage I</option>
                            <option>Hypertension Stage II</option>
                            <option>Hypertensive Crisis</option>
                        </select>
                    </div>
                    <div className="col-6">
                        <label htmlFor="bp_comments" className="form-label">Comments:</label>
                        <textarea className="form-control" name="bp_comments" id="bp_comments" cols="10" rows="2"></textarea>
                    </div>
                </div>

                <hr />

                <h3>Participant Record</h3>
                <hr />

                <div className="row mb-3">
                    {/* Type of Screening */}
                    <div className="col">
                        <label htmlFor="screen_type" className="form-label">Type of Screening</label>
                        <select className="form-select"
                            value={form.visit_type}
                            onChange={handleChange}
                            id="screen_type"
                            name="screen_type"
                        >
                            <option selected disabled>Please Select</option>
                            <option>Initial Screening</option>
                            <option>Follow Up Screening</option>
                            <option>Follow Up from Physician</option>
                        </select>
                    </div>
                    <div className="col">
                        <label htmlFor="first_time" className="form-label">First time blood screen at church?:</label>
                        <select className="form-select"
                            value={form.first_time}
                            onChange={handleChange}
                            id="first_time"
                            name="first_time"
                        >
                            <option selected disabled>Please Select</option>
                            <option>Yes</option>
                            <option>No</option>
                        </select>
                    </div>
                    <div className="col">
                        <label htmlFor="history">Brief History</label>
                        <textarea className="form-control" name="history" id="history" cols="30" rows="10"></textarea>
                    </div>
                </div>
                <div className="row mb-3">
                    <div className="col">
                        <label htmlFor="last_time" className="form-label">If first Blood Screen as church, when was the last time prior to this bp?:</label>
                        <input type="date" onChange={handleChange} />
                        <p>Selected Date: {date}</p>
                    </div>
                </div>
                <div className="row mb-3">
                    <div className="col">
                        <label htmlFor="follow_up" className="form-label">Is this a follow up BP at church?:</label>
                        <select className="form-select"
                            value={form.follow_up}
                            onChange={handleChange}
                            id="follow_up"
                            name="follow_up" >

                            <option selected disabled>Please Select</option>
                            <option >Yes</option>
                            <option>No</option>
                        </select>
                    </div>
                    <div className="col">
                        <label htmlFor="diabetic_hx" className="form-label">any diabetic/pre-diabetic history?:</label>
                        <select className="form-select"
                            value={form.diabetic_hx}
                            onChange={handleChange}
                            id="diabetic_hx"
                            name="diabetic_hx" >
                            <option selected disabled>Please Select</option>
                            <option>Yes</option>
                            <option>No</option>
                        </select>
                    </div>
                </div>

                <div className="row mb-3">
                    <div className="col">
                        <label htmlFor="education" className="form-label">Education:</label>
                        <select className="form-select"
                            value={form.education}
                            onChange={handleChange}
                            id="education"
                            name="education">
                            <option selected disabled>Please Select</option>
                            <option>bp info</option>
                            <option>diabetes</option>
                            <option>nutrition</option>
                            <option>exercise</option>
                            <option>stress mgt.</option>
                            <option>smoking</option>
                            <option>weight</option>
                            <option>other</option>
                        </select>
                    </div>
                    <div className="col">
                        <label htmlFor="new_meds" className="form-label">New/Changed meds:</label>
                        <select className="form-select"
                            value={form.new_meds}
                            onChange={handleChange}
                            id="new_meds"
                            name="new_meds">
                            <option selected disabled>Please Select</option>
                            <option>Yes</option>
                            <option>No</option>
                        </select>
                    </div>
                    <div className="col">
                        <label htmlFor="medication_education" className="form-label">Medication Education:</label>
                        <select className="form-select"
                            value={form.medication_education}
                            onChange={handleChange}
                            id="medication_education"
                            name="medication_education">
                            <option selected disabled>Please Select</option>
                            <option>action</option>
                            <option>side effects</option>
                            <option>precautions</option>
                            <option>med card provided</option>
                        </select>
                    </div>

                </div>
                <div className="mb-3">
                    <label htmlFor="ref_to" className="form-label">Referral</label>
                    <select className="form-select"
                        value={form.ref_to}
                        onChange={handleChange}
                        id="ref_to"
                        name="ref_to" >
                        <option selected disabled>Please Select</option>
                        <option>To Health Care Provider</option>
                        <option>Assistance to Find a Provider</option>
                        <option>Community Resource/Agency</option>
                        <option>911/Urgent Care</option>
                    </select>
                </div>
                <div className="text-center">
                    <button type="submit" className="btn btn-primary" >Submit</button>
                </div>

            </form>
        </div>
    )
}