import Sidebar from "../../components/sidebar/Sidebar";
import Topbar from "../../components/topbar/Topbar";
import { useLocation, useNavigate } from "react-router-dom";
import "./client.css";
import client_info from "./test.json"
import "bootstrap/js/dist/tab";
import "./ClientContainer"


export default function Client(c) {
    const { state } = useLocation();
    const { client } = state;
    const nav = useNavigate()

    //const client = client_info


    const date = (theDate) => {
        const d = new Date(theDate)
        return d.toLocaleDateString();
    }

    const handleNewEncounter = () => {

        nav("/new-encounter", { state: { client: client } })
    }
    const handleNewBP = () => {

        nav("/new-bp", { state: { client: client } })
    }



    function handleSubmit() {
        //     e.preventDefault();
        //     const form = e.target;
        //     const formData = new FormData(form)
        //     const r = formData.getAll('race')
        //     console.log(r)
        //     formData.delete('race')
        //     formData.append("race", r)
        //     formData.append('age', age)

        //     let f = {}
        //     formData.forEach((value, key) => f[key] = value)
        //     const fJson = JSON.stringify(f)

        //     const data = {
        //         operation: "update_client",
        //         payload: {
        //             username: user.username,
        //             token: user.token,
        //             client_info: f
        //         }
        //     }

        //     console.log(data.payload.client_info)

        //     const res = await fetchData("POST", data)
        //     console.log(res)
        //     if (res['body']['success']) {
        //         console.log("success")
        //         nav("/client", { state: { client: { client } } })
        //     }
    }

    function handleUpdate() {
        const formInputs = document.getElementById('demoForm').elements;
        const updateButton = document.getElementById('updateDemoButton')
        const cancelButton = document.getElementById('clearDemoButton')
        const saveButton = document.getElementById('saveDemoButton')
        console.log(formInputs)

        for (let i = 0; i < formInputs.length; i++) {
            // Disable all form controls
            formInputs[i].setAttribute("readOnly", "false");
            formInputs[i].classList.remove('form-control-plaintext')
            formInputs[i].classList.add('form-control')
            formInputs[i].removeAttribute("readOnly")
        }


        console.log(formInputs)
        console.log(updateButton.classList)

        updateButton.classList.add("d-none")
        cancelButton.classList.remove('d-none')
        saveButton.classList.remove('d-none')

    }

    function handleCancel() {
        const formInputs = document.getElementById('demoForm').elements;
        const updateButton = document.getElementById('updateDemoButton')
        const cancelButton = document.getElementById('clearDemoButton')
        const saveButton = document.getElementById('saveDemoButton')

        document.getElementById('demoForm').reset()
        for (let i = 0; i < formInputs.length; i++) {
            // Disable all form controls
            formInputs[i].setAttribute("readOnly", "true");
            formInputs[i].classList.add('form-control-plaintext')
            formInputs[i].classList.remove('form-control')
            formInputs[i].setAttribute("readOnly", "")
        }

        updateButton.classList.remove("d-none")
        cancelButton.classList.add('d-none')
        saveButton.classList.add('d-none')
        console.log(client.client.ethnicity)

    }
    return (

        <div className="d-flex clientContainer">
            <div className="container-fluid px-0 mx-0">
                <Topbar page="client" />
                <div className="clientTop p-4 pt-1 d-flex justify-content-between">
                    <div className="row align-items-center">
                        <div className="col-auto">
                            {client.client.picture ? <img className="border border-2 border-white rounded-circle object-fit-cover me-2" alt="not found" width={"80px"} height={"80px"} src={client.client.picture} />
                                : <span><i className="bi bi-person-circle lh-1 text-tertiary opacity-50 me-2" style={{ fontSize: "80px", fontWeight: "100" }}></i></span>
                            }
                        </div>
                        <div className="col">
                            <div className="d-flex align-items-center">
                                <h1 className="me-2" style={{ fontFamily: 'var(--display-font)' }}>{client.client.first_name + " " + client.client.last_name}</h1>
                                <span className="">({client.client.client_id})</span>
                            </div>

                            <div className="d-flex">
                                <p className="me-2">
                                    <span className="fw-light">{client.client.age}, </span>
                                </p>
                                <p className="me-2">
                                    <span className="fw-light">{client.client.gender}</span>
                                </p>
                                <p className="me-2">•</p>
                                <p>
                                    <span className="fw-light">{date(client.client.dob)}</span>
                                </p>
                            </div>

                        </div>
                    </div>

                </div>
                <div className="clientNavbar">
                    <nav>
                        <div class="nav nav-underline justify-content-center" id="nav-tab" role="tablist">
                            <button class="nav-link active" id="nav-home-tab" data-bs-toggle="tab" data-bs-target="#nav-home" type="button" role="tab" >Demographics</button>
                            <button class="nav-link" id="nav-profile-tab" data-bs-toggle="tab" data-bs-target="#nav-profile" type="button" role="tab" >Medical History</button>
                            {/* <button class="nav-link" id="nav-contact-tab" data-bs-toggle="tab" data-bs-target="#nav-contact" type="button" role="tab" >Past Interactions</button> */}
                            {/* <button class="nav-link" id="nav-disabled-tab" data-bs-toggle="tab" data-bs-target="#nav-disabled" type="button" role="tab" >Medications</button>
                            <button class="nav-link" id="nav-disabled-tab" data-bs-toggle="tab" data-bs-target="#nav-disabled" type="button" role="tab" >Alergies</button> */}
                        </div>
                    </nav>
                </div>
                <div className="container-fluid container-xl mt-2">
                    <div className=" shadow rounded-4 overflow-scroll">
                        <div className="">

                            <div class="tab-content" id="nav-tabContent">
                                {/* Demographics Tab */}
                                <div class="tab-pane fade show active" id="nav-home" role="tabpanel" tabIndex="0">
                                    <div className="container-fluid bg-white border border-top-0">
                                        <div className="d-flex justify-content-between align-items-center pt-2">
                                            <h5 className="display-6 text-center">Overview</h5>
                                            <div className="">
                                                <button className="btn btn-sm btn-outline-secondary d-none" onClick={handleCancel} id="clearDemoButton">Cancel</button>
                                                <button className="btn btn-sm btn-primary d-none" onClick={handleUpdate} id="saveDemoButton">Save</button>
                                            </div>
                                            <button className="btn btn-sm btn-outline-secondary" onClick={handleUpdate} id="updateDemoButton">Edit</button>
                                        </div>
                                        <form method="POST" className="border border-2 p-4" id="demoForm" onSubmit={handleSubmit}>
                                            <div className="row p-3 pt-0 gx-0">
                                                <div className="col-sm col-md-6">
                                                    <div className="row mb-1">
                                                        <label htmlFor="staticAddress" className="col-form-labelcol-sm-6 col-md-4 ">Address:</label>
                                                        <div className="col-md-8 col-sm-6">
                                                            <input type="text" readOnly={true} className="form-control-plaintext form-control" id="staticAddress" defaultValue={client.client.address} />
                                                        </div>
                                                    </div>
                                                    <div className="row mb-1">
                                                        <label htmlFor="staticPhone" className="col-form-label col-md-4 col-sm-6">Phone:</label>
                                                        <div className="col-sm-8">
                                                            <input type="text" readOnly className="form-control-plaintext form-control" id="staticPhone" defaultValue={client.client.phone_number} />
                                                        </div>
                                                    </div>
                                                    <div className="row mb-1">
                                                        <label htmlFor="staticEmail" className="col-form-label col-sm-4">Email:</label>
                                                        <div className="col-sm-8">
                                                            <input type="text" readOnly className="form-control-plaintext form-control" id="staticEmail" defaultValue={client.client.email} />
                                                        </div>
                                                    </div>
                                                    <div className="row mb-1">
                                                        <label htmlFor="staticEMContact" className="col-form-label col-sm-4">Emergency Contact:</label>
                                                        <div className="col-sm-8">
                                                            <input type="text" readOnly className="form-control-plaintext form-control" id="staticEMContact" defaultValue={client.client.emergency_contact} />
                                                        </div>
                                                    </div>
                                                    <div className="row mb-1">
                                                        <label htmlFor="staticECPhone" className="col-form-label col-sm-4">Emegency Contact Phone:</label>
                                                        <div className="col-sm-8">
                                                            <input type="text" readOnly className="form-control-plaintext form-control" id="staticECPhone" defaultValue={client.client.emergency_phone_number} />
                                                        </div>
                                                    </div>
                                                    <div className="row mb-1">
                                                        <label htmlFor="staticOccupation" className="col-form-label col-sm-4">Occupation:</label>
                                                        <div className="col-sm-8">
                                                            <input type="text" readOnly className="form-control-plaintext form-control" id="staticOccupation" defaultValue={client.client.occupation} />
                                                        </div>
                                                    </div>
                                                    <div className="row mb-1">
                                                        <label htmlFor="staticInsurance" className="col-form-label col-sm-4">Insurance:</label>
                                                        <div className="col-sm-8">
                                                            <input type="text" readOnly className="form-control-plaintext form-control" id="staticInsurance" defaultValue={client.client.insurance} />
                                                        </div>
                                                    </div>
                                                </div>
                                                <div className="col">
                                                    <div className="row mb-3">
                                                        <div className="col">
                                                            <label htmlFor="ethnicity" className="form-label-sm">Ethnicity: </label>
                                                            <p>{client.client.ethnicity}</p>
                                                            <select name="ethnicity" id="ethnicity" className="form-select d-none" defaultValue={client.client.ethnicity} disabled>
                                                                <option>Hispanic or Latino</option>
                                                                <option>Not Hispanic or Latino</option>
                                                            </select>
                                                        </div>
                                                    </div>
                                                    <div className="row mb-3">
                                                        <div className="col">
                                                            <p>Race: </p>
                                                            <p>{client.client.race}</p>
                                                            <div className="d-none" id="raceForm">
                                                                <div className="form-check form-check-inline">
                                                                    <input type="checkbox" className="form-check-input" id="race1" value={"AN"} name="race" />
                                                                    <label htmlFor="race1" className="form-check-label">American Indian or Alaska Native</label>
                                                                </div>
                                                                <div className="form-check form-check-inline">
                                                                    <input type="checkbox" className="form-check-input" id="race2" value={"AS"} name="race" />
                                                                    <label htmlFor="race2" className="form-check-label">Asian</label>
                                                                </div>
                                                                <div className="form-check form-check-inline">
                                                                    <input type="checkbox" className="form-check-input" id="race3" value={"AA"} name="race" />
                                                                    <label htmlFor="race3" className="form-check-label">Black or African American</label>
                                                                </div>
                                                                <div className="row">
                                                                    <div className="col">
                                                                        <div className="form-check form-check-inline">
                                                                            <input type="checkbox" className="form-check-input" id="race4" value={"PI"} name="race" />
                                                                            <label htmlFor="race4" className="form-check-label">Native Hawaiian or Other Pacific Islander</label>
                                                                        </div>
                                                                        <div className="form-check form-check-inline">
                                                                            <input type="checkbox" className="form-check-input" id="race5" value={"WH"} name="race" />
                                                                            <label htmlFor="race5" className="form-check-label">White</label>
                                                                        </div>
                                                                        <div className="form-check form-check-inline">
                                                                            <input type="checkbox" className="form-check-input" id="race6" value={"NA"} name="race" />
                                                                            <label htmlFor="race6" className="form-check-label">Prefer not to disclose</label>
                                                                        </div>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>

                                                </div>
                                            </div>
                                        </form>
                                    </div>
                                </div>

                                {/* med history */}
                                <div class="tab-pane fade" id="nav-profile" role="tabpanel" tabIndex="0">
                                    <div className="container-fluid bg-white border border-top-0">
                                        <div className="d-flex justify-content-between align-items-center pt-2">
                                            <h5 className="display-6 text-center">History</h5>
                                            <div className="">
                                                <button className="btn btn-sm btn-outline-secondary d-none" onClick={handleCancel} id="clearDemoButton">Cancel</button>
                                                <button className="btn btn-sm btn-primary d-none" onClick={handleUpdate} id="saveDemoButton">Save</button>
                                            </div>
                                            <button className="btn btn-sm btn-outline-secondary" onClick={handleUpdate} id="updateDemoButton">Edit</button>
                                        </div>

                                        <form method="POST" className="border border-2 p-4" id="medHistForm" onSubmit={handleSubmit}>
                                            <div className="row p-3 pt-0">
                                                <div className="col">
                                                    <div className="row">
                                                        <label htmlFor="staticPhysName" className="col-form-label col-sm-3">Primary Physician:</label>
                                                        <div className="col-sm-9">
                                                            <input type="text" readOnly className="form-control-plaintext form-control" id="staticPhysName" defaultValue={client.client.primary_physician_name} />
                                                        </div>
                                                    </div>
                                                    <div className="row">
                                                        <label htmlFor="staticPhysNumber" className="col-form-label col-sm-3">Primary Physician Number:</label>
                                                        <div className="col-sm-9">
                                                            <input type="text" readOnly className="form-control-plaintext form-control" id="staticPhysNumber" defaultValue={client.client.primary_physician_number} />
                                                        </div>
                                                    </div>
                                                    <div className="row">
                                                        <label htmlFor="staticMedication" className="col-form-label col-sm-3 ">Medications:</label>
                                                        <div className="col-sm-9">
                                                            <input type="text" readOnly className="form-control-plaintext form-control" id="staticMedication" defaultValue={client.client.medications} />
                                                        </div>
                                                    </div>

                                                    <div className="row">
                                                        <label htmlFor="staticHeight" className="col-form-label col-sm-3">Height:</label>
                                                        <div className="col-sm-9">
                                                            <input type="text" readOnly className="form-control-plaintext form-control" id="staticEmail" defaultValue={client.client.height} />
                                                        </div>
                                                    </div>
                                                    <div className="row">
                                                        <label htmlFor="staticWeight" className="col-form-label col-sm-3">Weight:</label>
                                                        <div className="col-sm-9">
                                                            <input type="text" readOnly className="form-control-plaintext form-control" id="staticEMContact" defaultValue={client.client.weight} />
                                                        </div>
                                                    </div>
                                                    <div className="row">
                                                        <label htmlFor="staticBMI" className="col-form-label col-sm-3">BMI:</label>
                                                        <div className="col-sm-9">
                                                            <input type="text" readOnly className="form-control-plaintext form-control" id="staticEMContact" defaultValue={client.client.BMI} />
                                                        </div>
                                                    </div>
                                                    <div className="row">
                                                        <label htmlFor="staticA1C" className="col-form-label col-sm-3">A1C:</label>
                                                        <div className="col-sm-9">
                                                            <input type="text" readOnly className="form-control-plaintext form-control" id="staticEMContact" defaultValue={client.client.A1C} />
                                                        </div>
                                                    </div>
                                                </div>

                                            </div>
                                        </form>
                                    </div>
                                </div>
                                <div class="tab-pane fade" id="nav-contact" role="tabpanel" tabIndex="0">
                                    <div className="container border border-top-0">
                                        <div className="d-flex justify-content-between align-items-center pt-2">
                                            <h5 className="display-6 text-center">History</h5>
                                            <div className="">
                                                <button className="btn btn-sm btn-outline-secondary d-none" onClick={handleCancel} id="clearDemoButton">Cancel</button>
                                                <button className="btn btn-sm btn-primary d-none" onClick={handleUpdate} id="saveDemoButton">Save</button>
                                            </div>
                                            <button className="btn btn-sm btn-outline-secondary" onClick={handleUpdate} id="updateDemoButton">Edit</button>
                                        </div>
                                    </div>
                                </div>
                                <div class="tab-pane fade" id="nav-disabled" role="tabpanel" tabIndex="0">...</div>

                            </div>

                        </div>
                    </div>


                    {/* right sidebar */}
                    <div className="col-auto mt-4">
                        <div className="card shadow-sm">
                            <div className="card-body">
                                <div className="container-fluid p-0 m-0">
                                    <nav className="navbar nav flex-column bg-body-tertiary">
                                        <h6 className="navbar-text">New Interactions</h6>
                                        <ul className="nav nav-pills nav-justified">
                                            <li className="nav-item">
                                                <button className="btn" onClick={handleNewEncounter}>One-to-One</button>
                                            </li>
                                        </ul>
                                        <ul className="nav nav-pills nav-justified">
                                            <li className="nav-item">
                                                <button className="btn " onClick={handleNewBP}>BP Screening</button>
                                            </li>
                                        </ul>
                                        <ul className="nav nav-pills nav-justified">
                                            <li className="nav-item">
                                                <a href="#" className="nav-link">Foot Care Screening</a>
                                            </li>
                                        </ul>
                                    </nav>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>



            </div>
        </div>
    )


}