import Sidebar from "../../components/sidebar/Sidebar";
import Topbar from "../../components/topbar/Topbar";
import { useContext, useEffect, useState } from "react";
import UserContext from "../../components/userContext/userContext";
import PatientCard from "../../components/patientCard/PatientCard";
import NewClientForm from "../../components/forms/NewClientForm";
import fetchData from "../../components/functions/apiRequest";
import "./dashboard.css"
import DashboardClient from "./DashboardClients";
import DashbaoardHome from "./DashboardHome";
import DashboardAdmin from "./DashboardAdmin";


const Dashboard = (page) => {
    const user = useContext(UserContext).user
    const [clients, setClient] = useState()
    const arr = []
    let clientList


    useEffect(() => {
        async function getData() {
            let data = {
                operation: 'get_user_client_list',
                payload: {
                    username: user.username,
                    token: user.token,
                }
            }
            const res = await fetchData('POST', data)
            console.log(res)

            if (!ignore && res['body']['success']) {
                setClient(res['body']["return_payload"]['client_list'])
                clientList = res['body']["return_payload"]['client_list']
                console.log(clientList)
            }
        }

        async function getUser() {
            let data = {
                operation: 'get_user',
                payload: {
                    username: user.username,
                    token: user.token,
                    user_to_find: user.username,
                    include_list: [
                        "first_name",
                        "last_name",
                        "address",
                        "phone_number",
                        "license_state",
                        "license_number",
                        "token",
                        "role"
                    ]
                }
            }
        }

        let ignore = false;
        getData()
        return () => {
            ignore = true;
        }
    }, []);

    // TODO: check for page refresh.
    // useEffect(() => {
    //     if (user.user == null){

    //     }
    // })


    function choosePage() {
        console.log(page.page)
        console.log(clients)
        if (clients) {
            switch (page.page) {
                case 'home':
                    return (<DashbaoardHome />)
                    break;
                case 'client':
                    return (<DashboardClient allClients={clients} />)
                    break;
                case 'forms':
                    return (
                        <>
                            <div className="container-fluid">
                                <div className="card p-2 bg-transparent border-0" >
                                    <div className="card-body">
                                        <div className="d-flex justify-content-between align-items-center">
                                            <h1 className="text-primary mx-3" style={{ fontFamily: 'var(--display-font)' }}> Group Encounters</h1>
                                        </div>
                                    </div>
                                </div>
                                <hr />
                                <div className="card">
                                    <div className="card-body">

                                    </div>
                                </div>

                            </div>
                        </>
                    )
                    break;
                case 'admin':
                    return (<DashboardAdmin />)
            }

        }
    }


    return (
        <>
            <div className="d-flex dash-body">
                <Sidebar />
                <div className="container-fluid px-0 mx-0">
                    <Topbar page="dashboard" />
                    {choosePage()}
                    {/*  */}
                </div>
            </div>


        </>
    )
}

export default Dashboard;